@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&family=Roboto+Mono:wght@100;400&family=Sacramento&display=swap);
*{
  color : #fff;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
          user-select: none;
  font-family: monospace;
}

#root{
  overflow-x: hidden;
  overflow-y: hidden;
  --side-back-color : rgb(53, 53, 53);
  --back-color : #000000;
  --base-color : #ff7b00;
  --base-animation-timer : 3s;
  --secondary-animation-timer : 1s;
  --big-font-size : 50px;
  --normal-font-size : 22px;
  --tiny-font-size : 15px;
  --base-shadow : 1px 1px 5px var(--base-color);
  max-height: 100vh;
}


.main-container *{
  width: 100vw;
  height: 100vh;
}

#activated-title{
  left: 0     ;
  top : 0     ;
  opacity: 1  ;
}

#activated-subtitle{
  font-size: inherit;
  margin-left : 1rem               ;
  color : var(--base-color)        ;
  text-shadow : var(--base-shadow) ;
  opacity : 1                      ;
  left : 0vw                       ;
}

.title{
  letter-spacing: -2px;
  height: auto;
  width: auto;
  position: relative;
  font-size: var(--big-font-size);
  text-align: center;
  opacity: 0;
  left : -50vw;
  transition: 
  opacity var(--base-animation-timer), 
  left var(--secondary-animation-timer),
  top var(--secondary-animation-timer);
}

.title__selected-text{
  font-family: inherit;
  font-size: inherit;
  opacity: 0;
  margin : 0;
  transition: 
  margin      var(--secondary-animation-timer), 
  text-shadow var(--secondary-animation-timer), 
  opacity     var(--secondary-animation-timer), 
  color       var(--secondary-animation-timer);
}




.main-container{
  display: flex;
  flex-direction: row;
  width: 500vw;
  height: 100vh;
  position: relative;
  left: 0vw;
  transition: left var(--secondary-animation-timer);
  background: linear-gradient(to right, var(--side-back-color) , var(--back-color) 30%, var(--side-back-color) );
}

@media (max-width : 1100px){
  #root{
    --big-font-size : 40px;
    --normal-font-size : 18px;
    --tiny-font-size : 15px;
  }
}

@media (max-width : 700px) {
  #root{
    --big-font-size : 30px;
    --normal-font-size : 14px;
    --tiny-font-size : 12px;
  }

}

@media (max-width : 400px) {
  #root{
    --big-font-size : 20px;
    --normal-font-size : 12px;
    --tiny-font-size : 9px;
  }
}

@media (max-width : 300px) {
  #root{
    --big-font-size : 13px;
    --normal-font-size : 9px;
    --tiny-font-size : 7px;
  }
}

@media (max-width : 200px){
    .about-container__title, .tecnos-container__title, .projects-container__title{
        display: none !important;
    }
}

.header-container{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--tiny-font-size);
}
.header-container__text{
    left : -100vw;
}





.about-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}


.about-container *{
    width: auto;
    height: auto;
    position: relative;
}
.about-container__paraph{
    left : 20vw;
    text-align: center;
    max-width: 40%;
    opacity: 0;
    font-size: var(--tiny-font-size);
    margin-right: 3vw ;
    transition: left 1s, opacity 1s;
}


.about-container__paraph__u{
    text-decoration: none;
    margin: 1vw;
    cursor: pointer;
    color : var(--base-color);
    transition: text-shadow .5s;
}

.activated-about-container{
    left : 0;
    opacity: 1;
}


.about-container__paraph__u:hover{
    text-shadow: var(--base-shadow);
}

@media screen and (max-width : 1000px){
    .about-container{
        flex-direction: column;
        align-items : center;
        justify-content: center;
        grid-gap: 5vh;
        gap: 5vh;
    }
    .about-container__paraph{
        text-align: center;
        max-width: 60%;
    }


}



.contact-content__paraph{
    font-size: var(--tiny-font-size);
}
.contact-content__title{
    left : -5vw;
    display: flex;
    align-items: center;
}

.paraph-part{
    position: relative;
    opacity: 0;
    font-size: var(--normal-font-size);
    transition-property: opacity top;
    transition-duration: var(--secondary-animation-timer);
    opacity : 0;
}
.contact-content__paraph__p1{
    top: -2vh;
}
.contact-content__paraph__p2{
    top: 4vh;
}
.paraph_activated{
    top : 0vw;
    opacity: 1;
}
.contact-content *{
    width: auto;
    height: auto;
    display: flex;
    justify-content: center;
}
.contact-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.copy-btn__img{
    transition : transform .5s;
    z-index: 99;
    font-size: var(--normal-font-size);
}
.copy-btn{
    padding: 1vw;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    opacity: 0;
    top: 20vh;
    border-radius: 8px;
    margin-top:2vh;
    margin-left: 20vw;
    cursor: pointer;
    transition: opacity var(--base-animation-timer), top var(--secondary-animation-timer);
}

.copy-btn__ACTIVATED{
    top: 0vw;
    opacity: 1;
}

.copy-btn__img:hover{
    transform: scale(1.2,1.2);
}

.success-msg{
    font-size: var(--tiny-font-size);
    position: relative;
    width: auto;
    left: -200%;
    transition: left .5s;
}

.success-msg__ACTIVATED{
    position: relative;
    left: 10%;
}


@media (max-width : 900px){
    .copy-btn{
        height: 2vh;
        margin-left: 40vw;
        padding: 4.5vw;
    }

}

@media (max-width : 700px){
    .copy-btn__img{
        font-size: var(--big-font-size);
    }
    .success-msg{
        font-size: var(--normal-font-size);
    }
}



.projects-container{
    display: flex;
    align-items: center;
    justify-content: space-around;
}


.projects-container *{
    height: auto;
    width: auto;
}
.projects-container__projects_list{
    margin : 0em 2em;
    max-height:100vh ;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 2rem;
    gap: 2rem;
}



.projects-container__title{
    display: flex;
    justify-content: center;
    align-items: center;
}



@media (max-width : 1300px){
    .projects-container{
        flex-direction: column;
        justify-content: center;
    }
    .projects-container__title{
        margin: 10vh;
    }
    .projects-container__projects_list{
        grid-template-columns: repeat(3,1fr);
        grid-gap: 1rem;
        gap: 1rem;
    }
}



@media (max-width : 400px){

    .projects-container__projects_list{
        margin-top: 10vh;
        grid-gap: .5rem;
        gap: .5rem;
    }
}
.project-item{
    display: flex;
    flex-direction: column;
    align-items : center;
    -webkit-user-select: none;
            user-select: none;
    text-decoration: none;
    padding: 2rem 1rem;
    position: relative;
    color : #000;
    background-color: var(--back-color);
    border-radius: 20px;
    opacity: 0;
    cursor: pointer;
    transition : opacity var(--secondary-animation-timer);
}
.project-title{
    text-align: center;
}


.project-type{
    font-size: var(--tiny-font-size);
    opacity: 0;
    position: relative;
    transition-property: top opacity;
    transition-duration: 1.3s;
    margin-top: 1em;
}
.project-type__ACTIVATED{
    top: 0;
    opacity: .5;
}


.project-item__name{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: var(--tiny-font-size);
    width: inherit;
    height: inherit;
}

.project-item__ACTIVATED{
    opacity: 1;
}

.modal-container{
    --base-padding : 10px;
    display: flex;
    justify-content: center;
    align-items : center;
    z-index: 1;
    left: 0;
    top: 0;
    position: fixed;
    width: 0;
    height: 0;
    overflow: hidden;
    opacity: 0;
    background-color: transparent;
    transition: opacity .2s;
}

.displayed{
    display: flex;
    width: 100%;
    height: 100%;
    opacity: 1;
}
.project-display{
    overflow-y: scroll;
    overflow-x: scroll;
    box-shadow: var(--base-shadow);
    width: 80vw;
    height: 80vh;
    background-color : #000;
    border-radius: 20px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    scrollbar-width: thin;
}


.project-display-header{
    margin: 2em;
    width: 100%;
    display: flex;
    font-size: var(--normal-font-size);
    flex-direction: row;
    align-items : center;
    justify-content: center;
}
.content-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 5em;
}
.wil-container{
    max-width: 40%;
    font-size: var(--tiny-font-size);
    overflow-wrap: break-word;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items : center;
}
.video-container{
    padding-right: var(--base-padding);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items : center;
    width: 40%;
    height: 80%;
}
.project-item__video{
    max-height: 100%;
    max-width: 100%;
    border-radius: 20px;
    box-shadow: var(--base-shadow);
}


.close-button{
    cursor : pointer;
    margin-left: 1em;
}

.redirect-container{
    margin-top: 40px;
    font-size: var(--tiny-font-size);
}



@media (max-width : 1000px){
    .project-item{
        border-radius: 10px;
        padding: 2rem .5rem;
    }
}

@media (max-width : 800px){
    .project-display-header{
        margin-bottom: 3em;
    }
    .content-container{
        flex-direction: column;
    }
    .wil-container{
        max-width: 80%;
    }
    .video-container{
        padding-top: 1em;
        align-items : center;
    }

}
@media (max-width : 700px){
    .project-item{
        border-radius: 10px;
        padding: 1rem .2rem;
    }
    .video-container{
        width: 80%;
    }
}

@media (max-width : 500px){
    .project-item{
        border-radius: 10px;
        padding: 1rem .2rem;
    }
}

.tecnos-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-left: 1em;
}
.tecnos-container *{
    display: flex;
    justify-content: center;
    width: auto;
    height: auto;
}


.tecnos-container__title{
    display: flex;
    align-items: center;
    position: relative;
}


.tecnos-container__list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    gap: 10px;
    grid-column-gap: 50px;
    column-gap: 50px;
}
.tecnos-container__list__item{
    display: flex;
    font-size: var(--tiny-font-size);
    justify-content: left;
    align-items: center;
    padding: 1vw;
    transition: left var(--secondary-animation-timer), opacity var(--secondary-animation-timer);
    width: auto;
    opacity: 0;
    position: relative;
    left: -50vw;
}
.tecno_activated{
    left: 0vw;
    opacity: 1;
}
.tecno_check{
    font-size: var(--normal-font-size);
    color: var(--base-color);
    text-shadow: 0px 0px 4px var(--base-color);
    opacity: 0;
    transition: opacity var(--secondary-animation-timer), padding-right var(--secondary-animation-timer);
}
.tecno_check__activated{
    opacity: 1;
    padding-right:1vw ;
}
@media (max-width : 900px){
    .tecno_check{
        font-size :2vw;
    }

    .tecnos-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .tecnos-container__title{
        margin-bottom: 5vh;
    }
}

@media (max-width : 400px){
}
.map-bigger-container{
    width: 100vw;
    position: fixed;
    top: 2vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.map-container{
    display: flex;
    max-width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    filter: blur(3px);
    grid-gap : 20px;
    gap : 20px;
    transition: filter var(--base-animation-timer);
}

.map-container__ACTIVATED{
    filter: blur(0px);
}


@media (max-width : 700px) {

    .map-container{
        grid-gap : 10px;
        gap : 10px;
    }
}


.map-item{
    --animation-timing : .2s;
    cursor: pointer;
    font-size: var(--normal-font-size);
    padding: 10px;
    border-radius: 10px;
    transition-property: box-shadow;
    transition-duration: var(--animation-timing);
}

.map-item__SELECTED{
    box-shadow: 0 0 0 2px var(--base-color) !important;
}
.map-item:hover{
    box-shadow: 0 0 0 2px #414141;
}


@media (max-width : 700px) {
    .map-item{
        padding: 10px;
    }
}

@media (max-width : 400px) {
    .map-item{
        padding: 5px;
        border-radius : 5px;
    }
}




